<template lang="pug">
    .programme__item-content
        .programmes__point-number {{index+1}}
        input(v-model='editedPoint.name'  rows="2" :disabled='!editMode').programmes__point-name
        textarea(v-model='editedPoint.description'  rows="2" :disabled='!editMode').programmes__point-desc
        input(type='number' v-model='editedPoint.hours' :disabled='!editMode').programmes__point-duration
        .programmes__point-tools
            button(@click.prevent='editMode=!editMode' v-if='!editMode').programme__point-edit
            button(@click.prevent='saveChanges()' v-if='editMode').programme__point-save
            button(@click='resetChanges()' v-if='editMode').programme__point-reset
            button(@click='deletePoint()' v-if='!editMode').programme__point-remove
</template>
<script>
import { mapActions } from "vuex";

export default {
    data() {
        return {
            disabled: 0,
            editMode: false,
            editedPoint: {
                id: this.point.id,
                name: this.point.name,
                description: this.point.description,
                hours: this.point.hours
            }
        }
    },
    props: {
        point: Object,
        index: Number,
        programmeId: Number
    },
    methods: {
        ...mapActions("programmePoints", ["editProgrammePoint", "deleteProgrammePoint"]),
        saveChanges(){
            this.editProgrammePoint(this.editedPoint)
            this.editMode=!this.editMode
        },
        resetChanges(){
            this.editedPoint = {
                id: this.point.id,
                name: this.point.name,
                description: this.point.description,
                hours: this.point.hours
            }
            this.editMode=!this.editMode
        },
        deletePoint() {
            var data = {
                id: this.programmeId,
                pointId: this.point.id
            }
            this.deleteProgrammePoint(data)
        }
    }
}
</script>
